<template>
  <div>
    <img class="bg" src="@/assets/img/bg.jpg">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">
      <h1 class="h1"></h1>
                                                                  <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangonggonghao">
        <el-input v-model="ruleForm.gonghao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongxingming">
        <el-input v-model="ruleForm.xingming" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongmima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongnianling">
        <el-input v-model="ruleForm.nianling" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongzhicheng">
        <el-input v-model="ruleForm.zhicheng" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangonglianxidianhua">
        <el-input v-model="ruleForm.lianxidianhua" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongshenfenzheng">
        <el-input v-model="ruleForm.shenfenzheng" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongjiatingzhuzhi">
        <el-input v-model="ruleForm.jiatingzhuzhi" placeholder=""></el-input>
      </el-form-item>
                                          <el-button @click="login()" class="btn-login" type="primary"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
              },
      tableName:"",
      rules: {}
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
  },
  methods: {
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    login() {
                                                                                                                                                                                                                                                                                                                                    if(`yuangong` == this.tableName && this.ruleForm.lianxidianhua&&(!this.$validate.isMobile(this.ruleForm.lianxidianhua))){
        this.$message.error(``);
        return
      }
                                                                        if(`yuangong` == this.tableName && this.ruleForm.shenfenzheng&&(!this.$validate.checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(``);
        return
      }
                                                                                                this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${this.tableName}/register`,
            method: "post",
            data:this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$router.replace({ path: "/login" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
